import React, {useState} from 'react'

export default function Textform(props) {
    

const handleUpClick = () =>{
    // console.log('Text was change to uppercase');
    let newText = text.toUpperCase();
    setText(newText)
}
const handleLowClick = () =>{
    // console.log('Text was change to uppercase');
    let newText = text.toLowerCase();
    setText(newText)
}
    const handleClearClick = () =>{
    let newText = ('');
    setText(newText)
}
    const handleOnChange = (event) =>{
        // console.log('On Change');
        setText(event.target.value);
    }

   const handleCopy = ()=>{
       var text = document.getElementById("myBox");
       text.select();
       navigator.clipboard.writeText(text.value);
   }


    const [text, setText] = useState('');
    // setText("PRANZUL");
    return (
        <>
 <div className="container" style={{color: props.mode === 'dark'?'white':'black' }}>
        <h1>{props.heading}</h1>
        
       <div className="mb-3">
         <textarea  className="form-control" id="myBox" onChange={handleOnChange} style={{backgroundColor: props.mode === 'dark'?'grey':'white' , color: props.mode === 'dark'?'white':'grey'  }} rows='8' value = {text} >
         </textarea> 
       </div>
        <button className="btn btn-warning my-2 mx-2" onClick={handleUpClick}>Convert to UpperCase</button>
        <button className="btn btn-warning my-2 mx-2" onClick={handleLowClick}>Convert to LowerCase</button>
        <button className="btn btn-warning my-2 mx-2" onClick={handleClearClick}>Clear</button>
        <button className="btn btn-warning my-2 mx-2" onClick={handleCopy}>Copy</button>
      
 </div>
 <div className="container my-3" style={{color: props.mode === 'dark'?'white':'black' }}>
     
     <h1>Your Text Summary </h1>
     <p>{text.split(" ").length} words and {text.length} Char</p>
 </div>

 </>
    )
}
