import React from 'react'
import PropTypes from 'prop-types'
// import { Link } from 'react-router-dom'

export default function Navbar(props) {
    return (
        <div>
             <nav className={`navbar navbar-expand-lg navbar- ${props.mode} bg-${props.mode}`}>
  <div className="container-fluid">
    <a className="navbar-brand" href="/">{props.title}</a>
    {/* <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button> */}
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
        <form className="d-flex">
                <div className={`form-check form-switch text- ${props.mode === 'light'?'dark':'light'}`}>
          <input className="form-check-input mx-3" onClick={props.enableDarkMode} type="checkbox" role="switch" id="flexSwitchCheckDefault"/>
          <label className="form-check-label mx-3" htmlFor="flexSwitchCheckDefault">Dark Mode</label>
        </div>
      </form>
          {/* <a className="nav-link active" style={{color: props.mode === 'dark'?'white':'black' }} aria-current="page" href="#">Home</a> */}
        </li>
        <li className="nav-item">
       {/* <Link className="nav-link" style={{color: props.mode === 'dark'?'white':'black' }} to="/About">About</Link> */}
        </li>
      </ul>
      
    </div>
  </div>
</nav>
        </div>
    )
}

Navbar.propTypes = {title: PropTypes.string}
// Navbar.defaultProps = {title: 'Pranzul'}